import React,{useState,useEffect} from "react";
import $ from "jquery";

export default function Room(props){
  const socket = props.socket;
  const user = props.data.user;
  const idd = props.data.id;
  const [isc,setIsc] = useState(false);
  const [list,setList] = useState([]);
  const [rq,setRq] = useState(0);
  const [isSend,setSend] = useState(false);
  const [h,setHeight] = useState(window.innerHeight-150);
  const ids = user[0].id+"|"+user[1].id;
  const getId = (idd[0] == socket.id) ? idd[1] : idd[0];
      
  
  const main = $("#main");
  useEffect(()=>{
    socket.on("chat",mes => {
      setList(mes.list);
      main.scrollTop(main.prop("scrollHeight"));
    })
  });
  useEffect(()=> {
    socket.on("dis",mes => {
      if(mes == getId){
        list.push({user:"center",mes:"Người Ấy Đã Thoát"});
        setList(list);
        main.scrollTop(main.prop("scrollHeight"));
        setIsc(true);
      }
    })
  });
  useEffect(()=>{
    socket.on("sending",()=>{
      setSend(true);
    })
    socket.on("dissend",()=>{
      setSend(false);
    })
  });
  function like(){
    
    socket.emit("chat",ids,{
        user : props.user,
        mes : 'like'
      });
  }
  function chat(event){
    if(event.key === 'Enter'){
      socket.emit("dissend",getId)
      var nd = event.target.value;
      list.push({user:nd,mes:nd})
      setList(list);
      event.target.value = "";
      setRq(rq+1);
      main.scrollTop(main.prop("scrollHeight"));
      socket.emit("chat",ids,{
        user : props.user,
        mes : nd
      });
    }
  }
  window.onresize = function(event) {
    setHeight(this.innerHeight-150);
    main.scrollTop(main.prop("scrollHeight"));
  }
  
  return (
    <div class="room">
      <div class="bar"><span class="back"> <i class="fas fa-long-arrow-left"></i> </span> <span class="name">Chat Với : {user[0].user == props.user ? user[1].user : user[0].user}</span></div>
      <div id="main"class="main"style={{height:h+"px"}}>
      
      { list.map(k => 
        <div class="lm">
          { k.user == "center"
          ? <div class="center">{k.mes}</div>
          : <div class={k.user == props.user ? 'right' : 'left'}>{k.mes == "like" ? <i class="fal fa-thumbs-up"></i> : k.mes}</div>
          }
        </div>
        
      ) }
      { isSend && <div class="lm"><div class="left"><div class="spinner">
          <div class="bounce1"></div>
          <div class="bounce2"></div>
          <div class="bounce3"></div>
          </div></div></div>
      }
      </div>
      <div class="form">
        { isc 
        ? <button class="cannel"onClick={()=> {socket.emit("disroom",getId);props.setCall() }}>Nhấn Để Thoát</button>
        : <div class="fl"><input type="text"onChange={()=> socket.emit("sending",getId) }placeholder="Nhập Nội Dung ..."onKeyPress={chat}/><span class="like"onClick={like}><i class="fal fa-thumbs-up"></i></span></div>
        }
      </div>
    </div>
  )
}