import React,{useState,useEffect} from "react";

export default function Index(props){
  const socket = props.socket
  const css = {
    alert : {
      padding : "15px",
    },
    find :{
      padding : "10px 20px",
      border : "1px solid blue",
      borderRadius : "10px",
      background : "none",
      outline :"none",
      fontSize : "20px",
      fontWeight : "500"
    },
    info : {
      fontWeight : "600",
      margin : "10px"
    },
    finding : {
      margin : "20px",
      fontWeight : "bold"
    },
    cannel : {
      padding : "5px 15px",
      border : "1px solid blue",
      borderRadius : "10px",
      background : "none",
      outline :"none",
      fontWeight : "bold",
      marginTop : "10px"
    }
  }
  
  const [isFind,setFind] = useState(false);
  function find(){
    socket.emit("find",socket.id);
    setFind(true);
  }
  function cannel(){
    socket.emit("rfind",socket.id);
    setFind(false);
  }
  useEffect(()=> {
    socket.on("finded",mes => {
      if(mes.id.includes(socket.id)){
        socket.emit("rfind",socket.id);
        props.setCall(mes);
      }
    })
  });
  return (
    <div class="index"style={{textAlign:"center"}}>
      { isFind
      ? <div style={css.finding}>
          <div>Đang Tìm Người Lạ ...</div>
          <button style={css.cannel}onClick={cannel}>Hủy</button>
        </div>
      : <Ifind css={css}click={find}/>
      }
    </div>
  )
}
function Ifind(props){
  const css = props.css;
  return (
    <div>
    <div style={css.alert}>
      Bấm nút bên dưới để bắt đầu chat và xin hãy lịch sự vì tất cả đều là người thật (^_^)!
      <div style={css.info}>Ứng Dụng Được Viết Bởi <a href='http://fb.com/poker.0910'>Poker</a></div>
    </div>
    <button onClick={props.click}style={css.find}>Bắt Đầu Chat</button>
    </div>
  )
}