import React,{useState,useEffect} from "react";
import io from "socket.io-client";
import './App.css';
import { Header,Home,Log } from "./components";

const socket = io();

function App(){
  const [login,setLogin] = useState(false);
  const [user,setUser] = useState("");
  function getCall(e){
    setUser(e);
    setLogin(true);
  }
  return (
    <div class="app">
      { login ? <Home user={user}socket={socket} /> : <Log setCall={getCall}socket={socket} /> }
    </div>
  )
}

export default App;
