import React from "react";
import Header from "./Header";
import { Room,Index } from "./HomeComponents";

export default function Home(props){
  const [isRoom,setIs] = React.useState(false);
  const [data,setData] = React.useState([]);
  function getCall(e){
    setData(e);
    setIs(true);
  }
  function getCall2(){
    setIs(false);
  }
  return (
    <div class="home">
      <Header user={props.user} />
      { isRoom ? <Room setCall={getCall2}user={props.user}data={data}socket={props.socket}/> : <Index setCall={getCall}socket={props.socket}/> }
    </div>
  )
}