import React from "react";

export default function Log(props){
  const [user,setUser] = React.useState("");
  function click(){
    
    props.socket.emit("log",user,mes => {
      if(mes){
        alert("user ton tai");
      } else {
        props.setCall(user);
      }
    })
  }
  return (
    <div class="log">
      <div class="form">
        <div class="title">Đăng Nhập</div>
        <input type="text"placeholder="Nhập Tên ..."onChange={(e)=>setUser(e.target.value)} />
        <input type="button"onClick={click}value="Đăng Nhập"/>
      </div>
    </div>
  )
}